a {
    text-decoration: none;
}

.name-tag {
	text-align-last: center; 
	background: transparent; 
	margin-top: 50px;
}	

/* Header */

.my-bg-photo {
  background: url('../../images/krystal-ng-PrQqQVPzmlw-unsplash.jpg') no-repeat;
  background-size: cover;
  background-position: center center;
  position: relative;
}

.my-bg-photo * {
  position: relative;
  z-index: 10;
}

.my-bg-photo:before {
  content: " ";
  position: absolute;
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #000;
  opacity: .65;
}

/* ----END-----*/


/* Photo  */

.photo-bg {
    
  background: linear-gradient(to right, #cc99ff 0%, #ffccff 100%);
  background-size: cover;
  background-position: center center;
  position: relative;
}


.photo img {
    border-radius: 50%;
	box-sizing: border-box;
    margin: auto;
	margin-bottom: 20px;
display: block;
 max-width: 70%;
 margin-top: 30px;
	
}

/* ----END-----*/


/* Content */

.content-main {
    background-color: #E7B7C8;
}

/* ----END-----*/




