
.bg-photo {
  background: url('../../images/bbb.jpg') no-repeat;
  background-size: cover;
  background-position: center center;
  position: relative;
}

.bg-photo * {
  position: relative;
  z-index: 10;
}

.bg-photo:before {
  content: " ";
  position: absolute;
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #000;
  opacity: .65;
}


