.header {
  overflow: hidden;
  background: #6D5671;
  padding: 2px 2px;
}

.header a {
  float: left;
  color: white;
  text-align: center;
  padding: 5px;
  text-decoration: none;
  font-size: 17px; 
  line-height: 25px;
  border-radius: 4px;
}

.header a.logo {
  font-size: 17px;
}

.header a:hover {
  color:#120D06;
}

.header a.active {
  background-color: dodgerblue;
  color: white;
}

.header-right {
  float: right;
}

@media screen and (max-width: 700px) { 
 .header-right .hide-div {
	  display: none;
  }
  
  .header a {
    float: none;
    display: block;
    text-align: left;
  }
  
  .header-right {
    float: none;
  }
    
	  
}

/* Navigation Active */

.navbar__link--active {
  border-bottom: 2px solid #55056D;
  transition: border-bottom .5s ease-in-out;
}

/* ----END-----*/

/* Logo */

.header .fa {
 padding: 7px;
  font-size: 27px;
  width: 45px;
  text-align: center;
  text-decoration: none;
  margin: 3px 4px;
    border-radius: 50%;
}

.header .fa:hover {
  opacity: 0.7;
}

.header .fa-linkedin {
  background: white;
  color: black;
}

.header .fa-github {
    background: white;
  color: black;
}
/* ----END-----*/