.card {
  box-shadow: 0 8px 12px 0 rgba(0, 0, 0, 0.2);
  max-width: 300px;
  margin: auto;
  text-align: center;
  font-family: pangolin;
  padding: 10%;
  margin-bottom: 5%;
  cursor: pointer;
}

.card:hover {
	box-shadow: 25px 25px 20px 0 rgba(0, 0, 0, 0.2);
}	

.name {
  color: grey;
  font-size: 22px;
}

@media screen and (max-width: 600px) { 
.card {
  margin-bottom: 15%;
  
}


}