.positions {
    margin-top: 10%;
	margin-bottom: 10%;
}
.skillbg-photo {
  background: url('../../images/c.jpg') no-repeat;
  background-size: cover;
  background-position: center center;
  position: relative;
}

.skillbg-photo * {
  position: relative;
  z-index: 10;
}

.skillbg-photo:before {
  content: " ";
  position: absolute;
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #000;
  opacity: .65;
}