/* Header */

.artbg-photo {
  background: url('../../images/bg.jpg') no-repeat;
  background-size: cover;
  background-position: center center;
  position: relative;
}

.artbg-photo * {
  position: relative;
  z-index: 10;
}

.artbg-photo:before {
  content: " ";
  position: absolute;
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #000;
  opacity: .65;
}

/* ----END-----*/


/* Painting */

 .my-painting-container {
  max-width: 1000px;
     margin-top:50px;
    background: linear-gradient(to left, #99ccff 0%, #ff9999 100%);
     font-size: 17px;
	 transition: transform .5s;
}

.my-painting-container img:hover {
  transform: scale(1.05);
}

/* ----END-----*/