
/*FOOTER*/

footer {
  background: #16222A;
  background: -webkit-linear-gradient(59deg, #3A6073, #16222A);
  background:  linear-gradient(to right top, #7a80aa, #615f89, #483f68, #322248, #1d022a);;
  color: white;
  margin-top:100px;
  bottom: 0;
} 

footer a {
  color: #fff;
  font-size: 14px;
  transition-duration: 0.2s;
}

footer a:hover {
  color: orange;
  text-decoration: none;
}

.copy {
  font-size: 12px;
  padding: 10px;
  border-top: 1px solid #FFFFFF;
}

.footer-middle {
  padding-top: 2em;
  color: white;
}


/*SOCİAL İCONS*/

/* footer social icons */

ul.social-network {
  list-style: none;
  display: inline;
  margin-left: 0 !important;
  padding: 0;
}

ul.social-network li {
  display: inline;
  margin: 0 5px;
}


.fa {
 padding: 5px;
  font-size: 25px;
  width: 50px;
  text-align: center;
  text-decoration: none;
  margin: 5px 4px;
   
}

.fa:hover {
  opacity: 0.7;
}

.fa-github:hover {
  color: black;
}

.fa-facebook:hover {
  color: #3B5998;
}

.fa-instagram:hover {
  color: purple;
}

.fa-linkedin:hover {
  color: #007bb5;
}

