.skillcard {
  box-shadow: 0 8px 12px 0 rgba(0, 0, 0, 0.2);
  max-width: 300px;
  margin: auto;
  text-align: center;
  font-family: pangolin;
  background-color:white;
  padding: 10%;
  font-size: 20px;
  cursor: pointer;
}

.skillcard img {
	max-width: 35%;
}

.skillcard:hover {
	box-shadow: 25px 25px 20px 0 rgba(0, 0, 0, 0.2);
}

@media screen and (max-width: 600px) { 
.skillcard {
  margin-bottom: 15%;
  
}


}

